import {
  Checkmark,
  Close,
  EllipseOutline,
  OpenOutline,
} from "@vicons/ionicons5";
import type { DataTableColumn, DataTableRowKey } from "naive-ui";
import { h, reactive, ref, type Ref } from "vue";
import { RouterLink } from "vue-router";
import { useCulture } from "../../../core/culture";
import { apply } from "../../../core/mask";
import objectPath from "object-path";
import * as _ from "../../../core/underscore";
import Btn from "../buttons/Btn.vue";
import InputBox from "../inputs/InputBox.vue";
import ComboBox from "../inputs/ComboBox.vue";
import NumberBox from "../inputs/NumberBox.vue";
import CheckBox from "../inputs/CheckBox.vue";
import DateBox from "../inputs/DateBox.vue";
import Icon from "../display/Icon.vue";

const culture = useCulture();

const REM = 20; // fator de conversão de with (em rem) para px

type GridViewItemOptions<T> = {
  align?: "left" | "center" | "right";
  color?: "default" | "primary" | "info" | "success" | "warning" | "error";
  mode?: "default" | "link";
  icon?: any;
  mini?: boolean;
  width?: number;
  title?: string | ((row: T) => string);
  disabled?: (row: T) => boolean;
  format?: string;
  sort?: string; // indique o campo a ser ordenado (no caso de "render")
};

export class GridViewOptions<T> {
  private _data: Ref<T[]>;
  private _selectedItems: Ref<DataTableRowKey[]>;
  private _enableScrollX = false;

  public readonly key: string;
  public columns: any[]; // DataTableColumn[]
  public options: any;

  get data() {
    return this._data.value;
  }

  set data(value: T[]) {
    this._data.value = value;
  }

  /**
   * Retorna a lista de todas os IDs que estão selecionados. Lista vazia em caso de nenhum
   */
  get selectedItems() {
    return this._selectedItems.value;
  }

  /**
   * Retorna o ID do item selecionado (null em caso de nenhum)
   */
  get selectedItem(): DataTableRowKey | null {
    return this._selectedItems.value.length == 0
      ? null
      : this._selectedItems.value[0];
  }

  /**
   * Adiciona um ID na lista dos selecionados e marca na GridView
   */
  set selectedItem(value: DataTableRowKey | null) {
    this._selectedItems.value.length = 0;
    if (value != null) {
      this._selectedItems.value.push(value);
    }
  }

  /**
   * Cria uma nova instancia de GridViewOptions para ser utilizada no componente grid-view
   * @param key A chave primaria ou identificador unico da Dto
   */
  constructor(key: string) {
    this._data = ref([]);
    this._selectedItems = ref<DataTableRowKey[]>([]);
    this.key = key;
    this.columns = [];
    this.options = this.createOptions();
  }

  /**
   * Carrega a gridview utilizando uma função assincrona
   * @param asyncLoad Metodo assincrono que retorna um array do tipo T da gridview
   */
  async load(asyncLoad: () => Promise<T[]>) {
    this.options.loading = true;

    try {
      const values = await asyncLoad();
      this._data.value = values;
    } finally {
      this.options.loading = false;
    }
  }

  /**
   * Indica que a GridView terá seleção multipla (checkbox)
   */
  multiple(options?: { disabled?: (row: T) => boolean }): GridViewOptions<T> {
    this.columns.push({
      type: "selection",
      multiple: true,
      width: 2 * REM,
      disabled: options?.disabled,
    });
    return this;
  }

  /**
   * Indica que a GridView terá seleção simples (radio)
   */
  select(options?: { disabled?: (row: T) => boolean }): GridViewOptions<T> {
    this.columns.push({
      type: "selection",
      multiple: false,
      width: 2 * REM,
      disabled: options?.disabled,
    });
    return this;
  }

  /**
   * Adiciona suporte a scroll horizontal na tabela.
   * As colunas definidas antes destes metodos serão fixadas a esquerda
   * A largura total da tabela é computada pela somas dos width de cada coluna. Quando uma GridView utiliza
   * este metodo, todas as colunas de tamanho variavel devem estar preenchidas para o scroll funcionar corretamente
   */
  freeze() {
    // fixa todas as colunas anteriores a esquerda
    for (let i = 0; i < this.columns.length; i++) {
      this.columns[i].fixed = "left";
      if (!this.columns[i].width)
        alert(
          "O metodo GridViewOptions.freeze() encontrou colunas sem definição de tamanho antes de chamar o metodo"
        );
    }

    this._enableScrollX = true;

    return this;
  }

  /**
   * Adiciona uma coluna do tipo botão na gridview
   * @param text Texto a exibir no botão
   * @param onclick Evento do click do botão
   * @param options Opções de renderização (align, disabled, width, color, mode, title, icon)
   */
  button(
    text: string,
    onclick: (row: T) => void,
    options?: GridViewItemOptions<T>
  ): GridViewOptions<T> {
    const opts: DataTableColumn = {
      key: text,
      title: " ",
      align: options?.align,
      disabled: options?.disabled,
      width: getWidth(options?.width, 3),
      render(row: any) {
        return h(
          Btn,
          {
            color: options?.color || "default",
            mode: options?.mode || "default",
            mini: options?.mini,
            title: options?.title,
            icon: options?.icon,
            onClick: () => onclick(row),
          },
          { default: () => text }
        );
      },
    };

    this.columns.push(opts);

    return this;
  }

  /**
   * Adiciona uma coluna do tipo botão com um icone na GridView
   * @param name Referencia do icone. Utilizar import { NomeIcone } from "@vicons/ionicons5"
   * @param onclick Metodo para capturar o evento de click neste icone
   * @param options Opções de renderização (width, align, title, color, disabled)
   */
  icon(
    icon: any,
    onclick: (row: T) => void,
    options?: GridViewItemOptions<T>
  ): GridViewOptions<T> {
    const opts: DataTableColumn = {
      key: icon.name,
      title: " ",
      align: options?.align || "center",
      width: getWidth(options?.width, 2),
      render(row: any) {
        const disabled = options?.disabled && options?.disabled(row);
        return h(Icon, {
          name: icon,
          title: options?.title,
          style: {
            //--default-font, primary-color
            color: `var(--${
              disabled
                ? "disabled-color"
                : options?.color
                ? options.color + "-color"
                : "default-font"
            })`,
            cursor: disabled ? "not-allowed" : "pointer",
          },
          onclick: () => {
            if (disabled) return;
            onclick(row);
          },
        });
      },
    };

    this.columns.push(opts);

    return this;
  }

  /**
   * Adiciona uma coluna a GridView do tipo edição de registro. Pode renderizar apenas um icone (quando o parametro onClickOrUrl for uma função)
   * ou renderizar um link com um icone apontando para o template de navegacao.
   * Um template de navegação é representado por ex "/pat/processo/{idProcesso}" aonde idProcesso é um field do objeto row T
   * @param onClickOrUrl Quando um metodo, dispara no click do icone passando a row T. Se uma string, é um template de navegação
   * @param options Opções de renderização ()
   */
  edit(
    onClickOrUrl: ((row: T) => void) | string,
    options?: GridViewItemOptions<T>
  ) {
    // define opções padrões
    const iconOptions = {
      title: "Editar registro",
      color: "primary",
    };
    Object.assign(iconOptions, options);

    // caso seja um metodo, apenas renderiza um icone
    if (typeof onClickOrUrl === "function")
      return this.icon(OpenOutline, onClickOrUrl, iconOptions);

    // caso seja string, gera um novo RouterLink
    const opts: DataTableColumn = {
      key: "edit",
      title: " ",
      align: options?.align || "center",
      width: getWidth(options?.width, 2),
      render(row: any) {
        return h(
          RouterLink,
          {
            to: onClickOrUrl.replace(/(\{[\w\.]+\})/g, (m: string) => {
              const field = m.substring(1, m.length - 1);
              return objectPath.get(row, field);
            }),
          },
          () =>
            h(Icon, {
              name: OpenOutline,
              title: options?.title || "Editar registro",
              style: {
                //--default-font, primary-color
                color: `var(--${
                  options?.color ? options.color + "-color" : "primary-color"
                })`,
                cursor: "pointer",
              },
            })
        );
      },
    };

    this.columns.push(opts);

    return this;
  }

  /**
   * Adiciona uma coluna na gridview com os dados vinculadas ao field do dataSource
   * @param header Titulo na header
   * @param key Chave/Caminho para obter o dado do objeto
   * @param width Largura da coluna (em 'rem'). Sempre deixar uma das colunas sem este parametro (= null) para o responsivo
   * @param options Opções de renderização (align, disabled, format)
   */
  column(
    header: string,
    field: string,
    width?: number,
    options?: GridViewItemOptions<T>
  ): GridViewOptions<T> {
    const opts: DataTableColumn = {
      title: header,
      key: field,
      width: getWidth(width, undefined),
      align: options?.align,
      disabled: options?.disabled,
      sorter: (row1, row2) => customSort(field, row1, row2),
    };

    if (options?.format) {
      opts.render = (row: any) => {
        const value = objectPath.get(row, field);
        // se o valor a exibir for uma string, formata como mascara
        if (typeof value === "string") return apply(value, options.format);

        // senão formata data/numero
        return culture.format(value, options.format);
      };
    }

    this.columns.push(opts);

    return this;
  }

  /**
   * Adiciona uma coluna que testa o campo (que deve ser boolean) e exibe um icone de Check ou X (ex: fgAtivo)
   * @param header Texto a exibir no botão
   * @param field Nome do campo do objeto T
   * @param options Opções de renderização (align, width)
   */
  boolean(
    header: string,
    field: string,
    width?: number,
    options?: GridViewItemOptions<T>
  ): GridViewOptions<T> {
    const opts: DataTableColumn = {
      key: field,
      title: header,
      align: options?.align || "center",
      width: getWidth(width, 2),
      sorter: (row1, row2) => customSort(field, row1, row2),
      render(row: any) {
        const result = objectPath.get(row, field);
        return h(Icon, {
          name:
            result === true
              ? Checkmark
              : result === false
              ? Close
              : EllipseOutline,
        });
      },
    };

    this.columns.push(opts);

    return this;
  }

  /**
   * Renderiza um componente ou um texto ou componente no lugar da coluna. Utilizado para condicionais ou colunas avançadas
   * @param header Titulo na header
   * @param width Largura da coluna (em rem)
   * @param fn Função de renderização. Retorna um componente ou um texto
   * @param options Opções de renderização (align, disabled, sort)
   */
  render(
    header: string,
    width: number | null = null,
    fn: (row: T, index?: number) => string | any,
    options?: GridViewItemOptions<T>
  ) {
    const opts = {
      title: header,
      width: getWidth(width, undefined),
      align: options?.align,
      disabled: options?.disabled,
      render: fn,
    };

    if (options?.sort) {
      opts.sorter = (row1, row2) => customSort(options.sort, row1, row2);
    }

    this.columns.push(opts);

    return this;
  }

  createControl(
    control: any,
    bindName: string,
    { header, field, width, controlOptions, options }
  ) {
    const renderFn = (row: T) => {
      const defaultOptions = {
        [bindName]: objectPath.get(row, field),
        ["onUpdate:" + bindName]: (v) => objectPath.set(row, field, v),
        disabled: options?.disabled ? options?.disabled(row) : false,
      };
      Object.assign(defaultOptions, controlOptions);
      return h(control, defaultOptions);
    };

    return this.render(header, width, renderFn, options);
  }

  /**
   * Renderiza um componente do tipo InputBox e faz bind do value com a field informado.
   * @param header Titulo na header
   * @param field Nome do campo
   * @param width Largura da coluna (em rem)
   * @param inputOptions Opções de customização do objeto InputBox (suas propriedades/eventos)
   * @param options Opções de renderização (align, disabled, sort)
   */
  inputbox(
    header: string,
    field: string,
    width?: number,
    inputOptions?: any,
    options?: GridViewItemOptions<T>
  ) {
    return this.createControl(InputBox, "value", {
      header,
      field,
      width,
      controlOptions: inputOptions,
      options,
    });
  }

  /**
   * Renderiza um componente do tipo ComboBox e faz bind do value com a field informado. Para popular a combo, utilize comboOptions
   * @param header Titulo na header
   * @param field Nome do campo
   * @param width Largura da coluna (em rem)
   * @param comboOptions Opções para customizar o componente de ComboBox. Utilize este campo para popular a combo. { options: [...], valueKey: "idPessoa", textKey: "noPessoa" })
   * @param options Opções de renderização (align, disabled, sort)
   */
  combobox(
    header: string,
    field: string,
    width?: number,
    comboOptions?: any,
    options?: GridViewItemOptions<T>
  ) {
    return this.createControl(ComboBox, "value", {
      header,
      field,
      width,
      controlOptions: comboOptions,
      options,
    });
  }

  /**
   * Renderiza um componente do tipo NumberBox e faz bind do value com a field informado.
   * @param header Titulo na header
   * @param field Nome do campo
   * @param width Largura da coluna (em rem)
   * @param numberOptions Opções para customizar o componente de NumberBox.
   * @param options Opções de renderização (align, disabled, sort)
   */
  numberbox(
    header: string,
    field: string,
    width?: number,
    numberOptions?: any,
    options?: GridViewItemOptions<T>
  ) {
    return this.createControl(NumberBox, "value", {
      header,
      field,
      width,
      controlOptions: numberOptions,
      options,
    });
  }

  /**
   * Renderiza um componente do tipo CheckBox e faz bind do checked (boolean) com a field informado.
   * @param header Titulo na header
   * @param field Nome do campo
   * @param width Largura da coluna (em rem)
   * @param checkboxOptions Opções para customizar o componente de CheckBox.
   * @param options Opções de renderização (align, disabled, sort)
   */
  checkbox(
    header: string,
    field: string,
    width?: number,
    checkboxOptions?: any,
    options?: GridViewItemOptions<T>
  ) {
    return this.createControl(CheckBox, "checked", {
      header,
      field,
      width,
      controlOptions: checkboxOptions,
      options,
    });
  }

  /**
   * Renderiza um componente do tipo DateBox e faz bind do date (Date) com a field informado.
   * @param header Titulo na header
   * @param field Nome do campo
   * @param width Largura da coluna (em rem)
   * @param dateboxOptions Opções para customizar o componente de DateBox.
   * @param options Opções de renderização (align, disabled, sort)
   */
  datebox(
    header: string,
    field: string,
    width?: number,
    dateboxOptions?: any,
    options?: GridViewItemOptions<T>
  ) {
    return this.createControl(DateBox, "date", {
      header,
      field,
      width,
      controlOptions: dateboxOptions,
      options,
    });
  }
  createOptions() {
    const self = this;
    return reactive({
      scrollX: () => {
        const width = this.columns
          .map((c) => c.width)
          .filter((v) => parseInt(v) > 0)
          .reduce((a, b) => a + b, 0);
        return width;
      }, // mantem undefined até chamar scrollX() na definição
      columns: this.columns,
      bordered: true,
      data: this._data,
      loading: ref(false),
      pagination: {
        pageSize: 10,
      },
      selectedItems: this._selectedItems,
      rowKey: (row: any) => objectPath.get(row, this.key),
      handleCheck(rowKeys: DataTableRowKey[]) {
        self._selectedItems.value = rowKeys;
      },
    });
  }
}

function getWidth(width?: number, defaultValue?: number) {
  if (width === null || width === undefined) return defaultValue * REM;
  return width * REM;
}

function customSort(field, row1, row2) {
  const value1 = objectPath.get(row1, field);
  const value2 = objectPath.get(row2, field);

  const r = _.compare(value1, value2);

  return r;
}
